<template>
<div style="height:100%">
  <Header></Header>
  <el-container style="height: calc(100% - .6rem); border: 1px solid #eee">
    <Aside></Aside>
    <el-main style="background:#fff;">
      <el-alert
        title=""
        type="warning">
        react-advanced
      </el-alert>
      <div class="bordermain">
        <div class="container">
          <el-card class="box-card">
            <div class="text item yufa">
              <!-- <p style="padding:0;">1</p> -->
              <p>1PropTypes,defaultPropsreact</p>
              <pre>
                <code>
                {{oneBug}}
                </code>
              </pre>
              <p>2 reactstatepropsrender</p>
              <p>3dom</p>
              <p>3-1 state</p>
              <p>3-2 jsx</p>
              <p>3-3  +  domjsdom['div', {'id': 'box'}, ['span', {}, 'hello World']]</p>
              <p>3-4 domdom</p>
              <p>3-5 state</p>
              <p>3-6 dom['div', {'id': 'box'}, ['span', {}, 'tao lin']]</p>
              <p>3-7 domdomspan</p>
              <p>3-8 domspan</p>
              <p>5 diffdom</p>

              <p>6 setStatesetState:this.setState(() => ({inputValue}), () => {console.log(123)})</p>
              <pre>
                <code>
                {{twoBug}}
                </code>
              </pre>
              <p>7 react</p>
              <img src="https://upload-images.jianshu.io/upload_images/8638758-70ff1cc0244b6f35.png" style="width:100%" alt="">
              <pre>
                <code>
                {{threeBug}}
                </code>
              </pre>
            </div>
            <el-card class="box-card">
            </el-card>
          </el-card>
        </div>
      </div>
    </el-main>
  </el-container>
</div>

</template>
<style scoped>
  .yufa p{
    margin-top: .2rem;
    padding-left: .3rem;
  }
  .container {
    width: 100%;
  }
  .bordermain {
    margin-top: .5rem;
    width: 100%;
    min-height: 2rem;
  }
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }
</style>

<script>
import Aside from '@/comp/aside';
import Header from '@/comp/header';
  export default {
    components: {
      Aside,
      Header
    },
    data() {
      return {
        oneBug: `
        // 1prop-types
        import PropTypes from 'prop-types';
        //1 prop
        TodoItem.propTypes ={
          // 1-1content
          content: PropTypes.string,
          // 1-2itemDelete
          itemDelete: PropTypes.func.isRequired,
          // 1-3 index
          index: PropTypes.oneOfType([PropTypes.bool,PropTypes.number])
        }
        // 1-4reacthttps://react.docschina.org/docs/typechecking-with-proptypes.html
        // 2
        TodoItem.defaultProps = {
          content: '123',
          index: 0
        }
                `,
        twoBug: `
        // refdom
          ref={(input)=> {this.input = input}}
        `,
        threeBug: `
          // 
          componentWillMount() {console.log('componentWillMount')};
          // 
          componentDidMount() {console.log('componentDidMount')}
          // truefalsefalse
          shouldComponentUpdate() {console.log('shouldComponentUpdate'); return true;}
          // 
          componentWillUpdate() {console.log('componentWillUpdate')}
          // 
          componentDidUpdate() {console.log('componentDidUpdate')}
          // 
          componentWillReceiveProps() {console.log('componentWillReceiveProps')}
          // componentWillUnmountdestroyed
          componentWillUnmount() {console.log('componentWillUnmount')}
        `,
      }
    }
  };
</script>


