<template>
<div style="height:100%">
  <Header></Header>
  <el-container style="height: calc(100% - .6rem); border: 1px solid #eee">
    <Aside></Aside>
    <el-main style="background:#fff;">
      <el-alert
        title=""
        type="warning">
        node
      </el-alert>
      <div class="bordermain">
        <div class="container">
          <el-card class="box-card">
            <div class="text item yufa">
              <p>nodehttp://news.baidu.com/</p>
              <pre>
                <code>
                {{oneBug}}
                </code>
              </pre>
            </div>
          </el-card>
          <el-card>
            <p><a href="https://segmentfault.com/a/1190000014811373?utm_source=tag-newest" target="_blank" rel="noopener noreferrer"></a></p>
          </el-card>
        </div>
      </div>
    </el-main>
  </el-container>
</div>

</template>
<style scoped>
  .yufa p{
    margin-top: .2rem;
    padding-left: .3rem;
  }
  .container {
    width: 100%;
  }
  .bordermain {
    margin-top: .5rem;
    width: 100%;
    min-height: 2rem;
  }
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }
</style>

<script>
import Aside from '@/comp/aside';
import Header from '@/comp/header';
  export default {
    components: {
      Aside,
      Header
    },
    data() {
      return {
        oneBug: `
          const express = require('express');
          const app = express();
          const superAgent = require('superagent');
          const cheerio = require('cheerio');
          const Nightmare = require('nightmare');
          const nightmare = Nightmare({show: true});
          let hotNews = [];
          let localNews = [];
          // nightmare
          nightmare
                  .goto('http://news.baidu.com/')
                  .wait('div#local_news')
                  .evaluate(() => document.querySelector("div#local_news").innerHTML)
                  .then(htmlStr => {
                    localNews = getLocalNews(htmlStr)
                  })
                  .catch(error => {
                    console.log(' -'+error)
                  })

          // 
          let getHotNews = (res) => {
            let hotNewsArr = [];
            let $ = cheerio.load(res.text);
            $('#pane-news ul li a').each((idx, ele) =>{
              hotNewsArr.push({
                title: $(ele).text(),
                href: $(ele).attr('href')
              })
            })
            return hotNewsArr
          }

          // 
          let getLocalNews = (htmlStr) => {
            let localNewsArr = [];
            let $ = cheerio.load(htmlStr);
            // 
            $('ul#localnews-focus li a').each((idx, ele) => {
              localNewsArr.push({
                title: $(ele).text(),
                href: $(ele).attr('href')
              })
            })
            $('div#localnews-zixun ul li a').each((idx, ele) => {
              localNewsArr.push({
                title: $(ele).text(),
                href: $(ele).attr('href')
              })
            })
            return localNewsArr;
          }
          // superagentnode
          superAgent.get('http://news.baidu.com/').end((err, res) => {
            if (err) {
              // 
              console.log(\` - \${err}\`)
            } else {
              // http://news.baidu.com/res
              // 
              hotNews = getHotNews(res);
              localNews = getLocalNews(res);
            }
          })
          // 
          app.get('/', async (req, res, next) => {
            res.send({hotNews, localNews});
          });

          app.listen(3003)
                `,
      }
    }
  };
</script>


