<template>
<div style="height:100%">
  <Header></Header>
  <el-container style="height: calc(100% - .6rem); border: 1px solid #eee">
    <Aside></Aside>
    <el-main style="background:#fff;">
      <el-alert
        title=""
        type="warning">
        fullPage
      </el-alert>
      <div class="bordermain">
        <div class="container">
          <el-card class="box-card">
            <div class="text item yufa">
            <p style="margin-top:.5rem;padding:0;"></p>
            <p>3<a href="http://fullpage.81hu.com/" target="_blank"></a></p>
            </div>
          </el-card>
          <pre>
            <code>
            {{oneBug}}
            </code>
          </pre>
          <pre>
            <code>
            {{twoBug}}
            </code>
          </pre>
        </div>
      </div>
		</el-main>
  </el-container>
</div>

</template>
<style scoped>
  .yufa p{
    margin-top: .2rem;
    padding-left: .3rem;
  }
  .container {
    width: 100%;
  }
  .bordermain {
    margin-top: .5rem;
    width: 100%;
    min-height: 2rem;
  }
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }
</style>

<script>
import Aside from '@/comp/aside';
import Header from '@/comp/header';
  export default {
    components: {
      Aside,
      Header
    },
    data() {
      return {
        twoBug: `
        // js
          $(document).ready(function() {
            $('#fullpage').fullpage({
              slidesNavigation: true,
              loopHorizontal: true,
              touchSensitivity: 15,//5100
              controlArrows: false,//slide,true
              resize : true,//false
              sectionsColor : ['#ccc', '#fff', 'pink', 'green'],//sectionbackground-color
              //events
              // indexnextIndex
              onLeave: function(index, nextIndex, direction){
                console.log(index, nextIndex, direction)
              },
              afterLoad: function(anchorLink, index){
                console.log(anchorLink, index)
              },
              afterRender: function(){
                // 
                console.log('afterRender')
              },
              afterResize: function(){
                console.log('afterResize')
              },
              // slide
              // 
              afterSlideLoad: function(anchorLink, index, slideAnchor, slideIndex){
                console.log(anchorLink, index, slideAnchor, slideIndex)
              },
              // 
              onSlideLeave: function(anchorLink, index, slideIndex, direction, nextSlideIndex){
                console.log(anchorLink, index, slideIndex, direction, nextSlideIndex)
              },
            });
          });
        `,
        oneBug:`
        // 1fullpage.min.cssjquery.min.jsjquery.fullpage.js
        // 2html
        <div id="fullpage">
          <div class="section active">Some section1</div>
          <div class="section">
              <!-- Some section2 -->
            <div class="slide"> Slide 1 </div>
            <div class="slide"> Slide 2 </div>
            <div class="slide"> Slide 3 </div>
            <div class="slide"> Slide 4 </div>
          </div>
          <div class="section">Some section3</div>
          <div class="section">Some section4</div>
        </div>
        `
      }
    },
  };
</script>


