<template>
<div style="height:100%">
  <Header></Header>
  <el-container style="height: calc(100% - .6rem); border: 1px solid #eee">
    <Aside></Aside>
    <el-main style="background:#fff;">
      <el-alert
        title=""
        type="warning">
        <a href="https://leetcode-cn.com/problemset/all/">leetCode</a>
      </el-alert>
      <div class="bordermain">
        <!-- <el-container>
          <el-card class="box-card">
            <div class="text item yufa">
            </div>
          </el-card>
        </el-container> -->
        <div class="container">
          <el-collapse >
            <el-collapse-item v-for="(item, index) in infoArr" :key="index" :title="(index+1)+ '.' +item.tip" :z-index='99999' :name="index">
              <div>{{item.html}}</div>
              <div>{{item.text}}</div>
              <pre>
                <code>
                {{item.example}}
                </code></pre>
            </el-collapse-item>
          </el-collapse>
        </div>
      </div>
    </el-main>
  </el-container>
</div>

</template>
<style scoped>
  .yufa p{
    margin-top: .2rem;
  }
  .container {
    width: 100%;
  }
  .bordermain {
    margin-top: .5rem;
    width: 100%;
    min-height: 2rem;
  }
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }
</style>

<script>
import Aside from '@/comp/aside';
import Header from '@/comp/header';
  export default {
    components: {
      Aside,
      Header
    },
    data() {
      return {
        infoArr: [
          {
            tip: '',
            html: ' nums targetLeetCodehttps://leetcode-cn.com/problems/two-sum',
            text: ' nums = [2, 7, 11, 15], target = 9 nums[0] + nums[1] = 2 + 7 = 9 [0, 1]',
            example: `
                get_sum(arr = [2,27,11,15,18,81], target = 99){
                let obj = {};
                let info
                arr.forEach((item, index) => {
                  if (!obj[item]) {
                    obj[item] = {'keyInde': index, 'targetData': target - item}
                    for (const key in obj) {
                      if (obj.hasOwnProperty(key)) {
                        const element = obj[key];
                        if (element.targetData === item) {
                          info = {'':key,'': element.keyInde, '2':element.targetData,'2':index}
                        }
                      }
                    }
                  }
                });
                console.log(info || 'target')
              }
                `,
          },
          {
            tip: '',
            html: ' n numsnums abc a + b + c = 0 LeetCodehttps://leetcode-cn.com/problems/3sum',
            text: ',  nums = [-1, 0, 1, 2, -1, -4][  [-1, 0, 1],  [-1, -1, 2]]',
            example: `
                let result = [];
                var repeatSet = new Set();
                for (let i = 0; i < arr.length; i++) {
                  for(let j = 0;j < arr.length; j++) {
                    for (let k = 0; k < arr.length; k++) {
                      if (arr[j] + arr[i] + arr[k] === 0) {
                        // res.push(arr[i], arr[j], arr[k])
                        var tmpResult = [arr[i],arr[j],arr[k]];
                        var tmpSortStr = String(tmpResult.sort());
                        if(!repeatSet.has(tmpSortStr)){
                            result.push(tmpResult);
                            repeatSet.add(tmpSortStr)
                        }
                      }
                    }
                  }
                }
                return result;
                `,
          },
          {
            tip: '',
            html: '',
            text: ': 120: 21 : -123: -321',
            example: `
                reverse (x) {
                  if (x === 0) return 0;
                  if (x.toString()[0] === '-') {
                    return x.toString()[0] + x.toString().slice(1).split('').reverse().join('') - 0
                  } else {
                    return x.toString().split('').reverse().join('') - 0
                  }
                }`,
          },
          {
            tip: '(i%num_people)',
            html: '  candies n = num_people   1  2  n  n+ 1  n+ 2  2 * n   num_people candies  ans[i]  i ',
            text: 'candies = 7, num_people = 4 [1,2,3,1]  ans[0] += 1 [1,0,0,0] ans[1] += 2 [1,2,0,0] ans[2] += 3 [1,2,3,0] ans[3] += 1 1  [1,2,3,1]',
            example: `var distributeCandies = function(candies, num_people) {
                      let res = new Array(num_people).fill(0)
                      let i = 0
                      while(candies !=0){
                          res[i%num_people]+= Math.min(i+1,candies)
                          // debugger
                          candies -= Math.min(i+1, candies)
                          i++
                      }
                      return res
                    };`,
          },
        ]
      }
    },
    created() {
      // console.log(this.get_sum())
      // console.log(this.reverse(-210))
      console.log(this.getthree_sum())
    },
    methods: {
      // 
      get_sum(arr = [2, 7, 11, 15], target = 9){
        let obj = {};
        let info
        arr.forEach((item, index) => {
          if (!obj[item]) {
            obj[item] = {'keyInde': index, 'targetData': target - item}
            for (const key in obj) {
              if (obj.hasOwnProperty(key)) {
                const element = obj[key];
                if (element.targetData === item) {
                  info = {'oneVal':key,'oneIndex': element.keyInde, 'twoVal':element.targetData,'twoIndex':index}
                }
              }
            }
          }
        });
        return [Number(info.oneVal),info.oneIndex,info.twoVal,info.twoIndex]
      },
      getthree_sum(arr = [-1, 0, 1, 2, -1, -4]){
        let result = [];
        var repeatSet = new Set();
        for (let i = 0; i < arr.length; i++) {
          for(let j = 0;j < arr.length; j++) {
            for (let k = 0; k < arr.length; k++) {
              if (arr[j] + arr[i] + arr[k] === 0) {
                // res.push(arr[i], arr[j], arr[k])
                var tmpResult = [arr[i],arr[j],arr[k]];
                var tmpSortStr = String(tmpResult.sort());
                if(!repeatSet.has(tmpSortStr)){
                    result.push(tmpResult);
                    repeatSet.add(tmpSortStr)
                }
              }
            }
          }
        }
        return result;
      },
      // 
      reverse (x) {
        if (x === 0) return 0;
        if (x.toString()[0] === '-') {
          return x.toString()[0] + x.toString().slice(1).split('').reverse().join('') - 0
        } else {
          return x.toString().split('').reverse().join('') - 0
        }
      }
    }
  };
</script>


