<template>
<div style="height:100%">
  <Header></Header>
  <el-container style="height: calc(100% - .6rem); border: 1px solid #eee">
    <Aside></Aside>
    <el-main style="background:#fff;">
      <el-alert
        title=""
        type="warning">
        nodeJS
      </el-alert>
      <div class="bordermain">
        <div class="container">
          <el-card class="box-card">
            <div class="text item yufa">
              <p>.htmlPC</p>
              <p>node</p>
              <pre>
                <code>
                {{oneBug}}
                </code>
              </pre>
            </div>
          </el-card>
          <el-card style="margin:10px 0;">
            <div>
              <pre>
                <code>
                <img src="../../assets/images/nodeImg.png" alt="">
                </code>
              </pre>
            </div>
          </el-card>
          <el-card>
            <p>1 2 hostnameip  3static</p>
          </el-card>
        </div>
      </div>
    </el-main>
  </el-container>
</div>

</template>
<style scoped>
  .yufa p{
    margin-top: .2rem;
    padding-left: .3rem;
  }
  .container {
    width: 100%;
  }
  .bordermain {
    margin-top: .5rem;
    width: 100%;
    min-height: 2rem;
  }
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }
</style>

<script>
import Aside from '@/comp/aside';
import Header from '@/comp/header';
  export default {
    components: {
      Aside,
      Header
    },
    data() {
      return {
        oneBug: `
          var http = require('http');
          var fs = require('fs');
          var url = require('url');
          var path = require('path');
          var hostname = '10.127.53.204';//ip
          var port = 3456; //
          //  
          // 1css,html,js
          // 2fs
          var servers = http.createServer(function(req, res) {
            // 1.1
            var mainUrl = url.parse(req.url);
            // 1.2
            var baseUrl = path.join(__dirname, 'static')
            // 1.3//index
            if (mainUrl.pathname === '/') {
              mainUrl.pathname += '/index.html'
            }
            // 1.4
            var filepath = path.join(baseUrl, mainUrl.pathname)
            // 2.1 tips:
            // ,
            fs.readFile(filepath, 'binary', function(err, data) {
              if (err) {
                throw err
              } else {
                res.write(data, 'binary');
                res.end()
              }
            })
          })
          servers.listen(port, hostname, function() {
            console.log('' +'http://'+hostname+':'+port)
          })
                `,
      }
    }
  };
</script>


