<template>
<div style="height:100%">
  <Header></Header>
  <el-container style="height: calc(100% - .6rem); border: 1px solid #eee">
    <Aside></Aside>
    <el-main style="background:#fff;">
      <el-alert
        title=""
        type="warning">
        javascriptstring
      </el-alert>
      <div class="bordermain">
        <el-container>
          <el-card class="box-card">
            <div class="text item yufa">
            <p>charAt()indexOf(), lastIndexOf(), substring(), slice(), substr(), replace(), search(), match(), split(), trim(), toLowerCase(), toUpperCase()</p>
            </div>
          </el-card>
        </el-container>
        <div class="container">
          <el-collapse >
            <el-collapse-item v-for="(item, index) in infoArr" :key="index" :title="(index+1)+ '.' +item.tip" :name="index">
              <div>{{item.html}}</div>
              <div>{{item.text}}</div>
              <div>{{item.example}}</div>
            </el-collapse-item>
          </el-collapse>
        </div>
      </div>
    </el-main>
  </el-container>
</div>

</template>
<style scoped>
  .yufa p{
    margin-top: .2rem;
  }
  .container {
    width: 100%;
  }
  .bordermain {
    margin-top: .5rem;
    width: 100%;
    min-height: 2rem;
  }
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }
</style>

<script>
import Aside from '@/comp/aside';
import Header from '@/comp/header';
  export default {
    components: {
      Aside,
      Header
    },
    data() {
      return {
        infoArr: [
          {
            tip: 'charAt()',
            html: 'str.charAt(n)',
            text: '===>  n  0~str.length-1',
            example: "string.vuevar str = 'javascript'; str.charAt(2); //'v'   str.charAt(12); //''",
          },
          {
            tip: 'indexOf()',
            html: 'indexOf(substr[,start])',
            text: `>  substr  str , start  -1 start 0 start < 0  0 start >= str.length -1 str.length.`,
            example: `var str = "javascript"; str.indexOf('s',6); // -1 str.indexOf('',11); // 10str.indexOf('',8); // 8`
          },
          {
            tip: 'lastIndexOf()',
            html: 'lastIndexOf(substr[,start])',
            text: '===>  n  0~str.length-1',
            example: "'lastindex'.lastIndexOf('a'); // 1",
          },
          {
            tip: 'substring()',
            html: 'str.substring(start[, end])',
            text: '= >  start  endstartend (end)start',
            example: `var str = 'abcdefg';
                      str.substring(1, 4); //"bcd"
                      str.substring(1); // "bcdefg"
                      str.substring(-1); //"abcdefg" 0`,
          },
          {
            tip: 'slice()',
            html: 'str.slice(start[,end])',
            text: '= >  start  end ',
            example: `var str = 'this is awesome';
                      str.slice(4, -1); //" is awesom"`,
          },
          {
            tip: 'substr()',
            html: 'str.slice(start[,end])',
            text: '= >  str start',
            example: `var str = "Just give me a reason";
                      str.substr(5, 10); // "give me a "
                      str.substr(-4, 2); // "as"`,
          },
          {
            tip: 'replace()',
            html: 'str.replace(regexp|substr, newSubStr|function)',
            text: '= >  str ',
            example: `var str = "do you love me";str.replace('love','hate'); // "do you hate me"`,
          },
          {
            tip: 'search()',
            html: 'str.search(regexp)',
            text: '= >  str  -1 new RegExp(obj) ',
            example: `var str = 'I love JavaScript!';
                      str.search(/java/); // -1
                      str.search(/Java/); // 7
                      str.search(/java/i); // 7
                      str.search('Java'); // 7`,
          },
          {
            tip: 'match()',
            html: 'str.match(regexp)',
            text: '= >  null new RegExp(obj) ',
            example: `var str = 'Javascript java';
                      str.match(/Java/); // ["Java"]
                      str.match(/Java/gi); // ["java", "Java"]
                      str.match(/ab/g); // null`,
          },
          {
            tip: 'split()',
            html: 'str.split([separator][, limit])',
            text: '= > separator ',
            example: `var str = "Hello?World!";
                      str.split(); // ["Hello?World!"]
                      str.split(''); // ["H", "e", "l", "l", "o", "?", "W", "o", "r", "l", "d", "!"]
                      str.split('?'); // ["Hello", "World!"]
                      str.split('',5); // ["H", "e", "l", "l", "o"]`,
          },
          {
            tip: 'trim()',
            html: 'str.trim()',
            text: '= >  str  str ',
            example: `var str = ' abc ';
                      str.trim(); // 'abc'
                      console.log(str); // ' abc '`,
          },
          {
            tip: 'toLowerCase()',
            html: 'str.toLowerCase()',
            text: '= >  str  str ',
            example: `var str = 'JavaScript';
                      str.toLowerCase(); // 'javascript'
                      console.log(str); // 'JavaScript'`,
          },
          {
            tip: 'toUpCase()',
            html: 'str.toUpCase()',
            text: '= >  str  str ',
            example: `var str = 'JavaScript';
                      str.toUpperCase(); // 'JAVASCRIPT'
                      console.log(str); // 'JavaScript'`,
          },
        ]
      }
    }
  };
</script>


