<template>
<div style="height:100%">
  <Header></Header>
  <el-container style="height: calc(100% - .6rem); border: 1px solid #eee">
    <Aside></Aside>
    <el-main style="background:#fff;">
      <el-alert
        title=""
        type="warning">
        apph5h5
      </el-alert>
      <div class="bordermain">
        <div class="container">
          <el-card class="box-card">
            <div class="text item yufa">
            <p style="padding:0;">VuesessionStorage</p>
            <p>1routermeta</p>
            <p>2router.beforeEach</p>
						<p>3sessionStorage</p>
            <p style="margin-top:.5rem;padding:0;"></p>
            <p>3<a href="https://blog.csdn.net/sweet_note/article/details/81073564" target="_blank"></a></p>
            </div>
          </el-card>
          <pre>
            <code>
            {{oneBug}}
            </code>
          </pre>
          <pre>
            <code>
            {{twoBug}}
            </code>
          </pre>
          <pre>
            <code>
            {{threeBug}}
            </code>
          </pre>
        </div>
      </div>
		</el-main>
  </el-container>
</div>

</template>
<style scoped>
  .yufa p{
    margin-top: .2rem;
    padding-left: .3rem;
  }
  .container {
    width: 100%;
  }
  .bordermain {
    margin-top: .5rem;
    width: 100%;
    min-height: 2rem;
  }
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }
</style>

<script>
import Aside from '@/comp/aside';
import Header from '@/comp/header';
  export default {
    components: {
      Aside,
      Header
    },
    data() {
      return {
        oneBug: `
        // 1meta:
                {
                  path: '/treeThree',
                  name: 'treeThree',
                  component: TreeThree,
                  meta:{
                    needLogin:true
                  }
                },
                {
                  path: '/login',
                  name: 'login',
                  component: Login,
                  meta:{
                    needLogin:true
                  }
                },
                {
                  path: '/forgetPw',
                  name: 'forgetPw',
                  component: ForgetPw
                },
                {
                  path: '/forgetNewPw',
                  name: 'forgetNewPw',
                  component: ForgetNewPw
                },
                `,
        twoBug: `
        // 2needLoginloginnext
          router.beforeEach((to, from, next) => {
            if(to.meta.needLogin){
              //sessionStorage
              if(window.sessionStorage.getItem('token')){
                next()
              }else{
              // /login
                if (to.path === '/login') { // next()
                  next();
                } else { //  
                  next({
                    path: '/login'
                  });
                }
              }
            }else{
              next()
            }
          })
					
        `,
        threeBug:`
          //3tokensessionStorage
          if (res.code === 0) {
          // tokensessionStorage
            window.sessionStorage.removeItem('token')
          // sessionStorage
            window.sessionStorage.setItem('token', JSON.stringify(res.data.token))
            this.$router.push('/')
          }
        `
      }
    }
  };
</script>


